
@charset "UTF-8";
.mu-th {
  text-align: center !important;
}
.mu-td {
  text-align: center !important;
}
.movie-poster {
  width: 80px;
  padding: 4px 0;
}
.movie-introduction {
  white-space: normal;
  padding: 4px 4px;
  letter-spacing: 1px;
  font-size: 14px;
  text-align: left;
  text-indent: 2em;
}

/*评分*/
.star {
  width: 30rem;
  height: 5.8rem;
  margin: 0 auto;
}
.star li {
  width: 6rem;
  height: 5.8rem;
  float: left;
  border: none;
  background: url(/static/homeIcon/starGrey.svg) no-repeat center;
  background-size: 80%;
}
.starItem {
  width: 6rem;
  height: 5.8rem;
  float: left;
  background-size: 80%;
  border: none;
}

/*评分对话框*/
#ratingIpt {
  margin-top: 3.2rem;
}
#ratingIpt li {
    margin-top: 1rem;
    width: 100%;
    height: 3.2rem;
    font-size: 2.6rem;
    box-shadow: none;
}
#ratingIpt li input, #ratingIpt li textarea, #ratingIpt li p {
      margin: 0 auto;
      width: 90%;
      height: 3.2rem;
      line-height: normal;
      text-align: center;
      color: #333333;
      box-shadow: none;
      border: 1px solid darkgray;
      border-radius: 6px;
      left: 5%;
      position: relative;
      outline: none;
}
#ratingIpt li p {
      margin: 3.8rem 0 0 0;
      text-align: center;
      font-size: 2.2rem;
      background: lightgray;
      color: white;
      height: 3.2rem;
      line-height: 3.2rem;
      border-radius: 3.2rem;
      border: none;
}
#ratingIpt li textarea {
      height: auto;
}

/*暂无电影*/
.ratingShowStyle {
  width: 100%;
  text-align: center;
  color: #494949;
  font-weight: bold;
  font-size: 1.6rem;
}

/*movie-show-ul*/
.movieShowUl {
  margin-top: 3.2rem;
  border-top: 1px solid lightgray;
}
.movieShowUl li {
    width: 100%;
    text-align: left;
}
.movieShowUl li img {
      width: 90%;
}
.movieShowUl li h3 {
      display: inline-block;
      margin: 0.4rem 10% 0.4rem 10%;
      font-size: 1.8rem;
      color: #17AA52;
}
.movieShowUl li:nth-child(2) {
    text-align: center;
}
.movieShowUl li:nth-child(3) p {
    color: #494949;
    font-size: 1.2rem;
    width: 90%;
    margin: 0 auto;
}
.movieShowUl li:nth-child(3) strong {
    color: #17AA52;
}
.ratingDel {
  width: 90%;
  text-align: center;
  font-size: 1.8rem;
  background: lightgray;
  height: 3.2rem;
  line-height: 3.2rem;
  border-radius: 3.2rem;
  color: white;
  margin: 0 auto;
  margin-bottom: 0.6rem;
  margin-top: 0.4rem;
}



